import { Button } from "@/components/ui/Button";
import { createCampaign } from "@/actions/campaigns";
import { prisma } from '@/lib/prisma';
import { notFound } from 'next/navigation';

export default async function NewCampaignPage({ params }: { params: Promise<{ slug: string }> }) {
    const { slug } = await params;
    const tenant = await prisma.tenant.findUnique({ where: { slug } });

    if (!tenant) notFound();

    const createCampaignWithTenant = createCampaign.bind(null, tenant.id);

    return (
        <div className="max-w-2xl mx-auto">
            <h1 className="text-2xl font-bold mb-6">Nueva Campaña</h1>

            <form action={createCampaignWithTenant} className="space-y-6 bg-white dark:bg-zinc-950 p-6 rounded-lg shadow border border-gray-200 dark:border-zinc-800">
                <div>
                    <label htmlFor="subject" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                        Asunto del Correo
                    </label>
                    <input
                        type="text"
                        name="subject"
                        id="subject"
                        required
                        placeholder="¡Novedades en Manay Academy!"
                        className="mt-1 block w-full rounded-md border-gray-300 dark:border-zinc-700 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm p-2 bg-transparent border"
                    />
                </div>

                <div>
                    <label htmlFor="content" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                        Contenido (HTML)
                    </label>
                    <textarea
                        name="content"
                        id="content"
                        rows={10}
                        placeholder="<p>Hola,</p><p>Te contamos que...</p>"
                        className="mt-1 block w-full rounded-md border-gray-300 dark:border-zinc-700 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm p-2 bg-transparent border font-mono"
                    />
                    <p className="mt-1 text-xs text-gray-500">
                        * Puedes usar HTML básico para dar formato.
                    </p>
                </div>

                <div className="flex justify-end pt-4">
                    <Button type="submit">Guardar Borrador</Button>
                </div>
            </form>
        </div>
    )
}
